import React from "react";
import { Link } from "gatsby";
import ReactGA from 'react-ga';

// Components
import SEO from "../SEO";
import ArticleCard from "../../components/ArticleCard";

import "./styles.scss";

export default class BlogListPage extends React.Component {
  componentDidMount() {
    ReactGA.initialize("UA-88285026-2");
    ReactGA.pageview(window.location.pathname + window.location.search);
  }

  render() {
    const {data, tag, title} = this.props;
    const posts = data.edges;
    const prevPageLink = data.pageInfo.currentPage === 2 ? `/${tag}` : `/${tag}/${data.pageInfo.currentPage - 1}`;
    const curPageLink = data.pageInfo.currentPage === 1 ? `${tag}` : `${tag}/${data.pageInfo.currentPage }`;
    return (
      <section className="BlogListPage">
        <SEO
          title={this.props.title}
          keywords={[title]}
          urlPath={curPageLink}
        />
        <h1 className="Title">{title}</h1>
        {posts.map(({ node }) => {
          return (
            <ArticleCard key={node.id} data={node} />
          );
        })}
        {data.pageInfo.hasPreviousPage &&
          <Link to={prevPageLink}>Prev</Link>
        }
        {data.pageInfo.hasNextPage &&
          <Link to={`/${tag}/${data.pageInfo.currentPage + 1}`}>Next</Link>
        }
      </section>
    );
  }
}